
import ReactDOM from 'react-dom';
import { store } from '../client/state';

export const handleInputChange = (instance, prop) => ((value) => {
  store.dispatch(instance.change(prop, value));
});

export function focusFirst(ctx) {
  const thisNode = ReactDOM.findDOMNode(ctx);
  const inputs = thisNode.querySelectorAll('input');
  if (inputs[0]) { inputs[0].focus(); }
}



